"use client";

import { useEffect, useState } from "react";

function getPreferredTheme(): "light" | "dark" {
	if (typeof window === "undefined") return "light";
	const stored = window.localStorage.getItem("theme");
	if (stored === "light" || stored === "dark") return stored;
	return window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light";
}

export function ThemeInitializer() {
	// Runs once to sync html class on mount in case inline script missed
	useEffect(() => {
		const theme = getPreferredTheme();
		const root = document.documentElement;
		if (theme === "dark") root.classList.add("dark");
		else root.classList.remove("dark");
	}, []);
	return null;
}

export default function ThemeToggle() {
    const [theme, setTheme] = useState<"light" | "dark">("light");
    const [mounted, setMounted] = useState(false);

    useEffect(() => {
        setMounted(true);
        const initial = getPreferredTheme();
        setTheme(initial);
    }, []);

    useEffect(() => {
        if (!mounted) return;
        const root = document.documentElement;
        if (theme === "dark") root.classList.add("dark");
        else root.classList.remove("dark");
        window.localStorage.setItem("theme", theme);
    }, [theme, mounted]);

    return (
        <button
            aria-label="Toggle dark mode"
            onClick={() => setTheme((t) => (t === "dark" ? "light" : "dark"))}
            className="inline-flex items-center gap-2 rounded-full px-3 py-1.5 text-sm bg-blue-50 text-blue-700 hover:bg-blue-100 focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-blue-500"
        >
            <span className="hidden sm:inline">{mounted ? (theme === "dark" ? "Light" : "Dark") : "Theme"} mode</span>
            <span aria-hidden>{mounted ? (theme === "dark" ? "‚òÄÔ∏è" : "üåô") : "üåì"}</span>
        </button>
    );
}
